import { Component, DoCheck } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';

@Component({
    selector:'header-navigation',
    templateUrl:'./header-navigation.component.html'
})

export class HeaderNavigationComponent implements DoCheck {
    
    lang:string = 'eng';
    navMenuActive:any;

    constructor(private multi:MultiLangService){ }

    ngOnInit(){
        this.navMenuActive = this.multi.navMenu.eng
    }

    onChange(){
        if(this.lang === 'eng'){
            this.multi.tlang.next(this.lang)
            this.navMenuActive = this.multi.navMenu.eng
        }
        else if(this.lang === 'som'){
            this.multi.tlang.next(this.lang)
            this.navMenuActive = this.multi.navMenu.som
        }
        else if(this.lang === 'arb'){
            this.multi.tlang.next(this.lang)
            this.navMenuActive = this.multi.navMenu.arb
        }
    }

    ngDoCheck(){
        this.multi.tlang.next(this.lang)
    }

    openDialog(){
        this.multi.openPrivacyPolicy(this.lang);
    }
}