import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Inject, Injectable } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { HttpModule } from '@angular/http';
import { environment } from '../environments/environment';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatDialogModule } from '@angular/material/dialog';

import { AppComponent } from './app.component';
import { HeaderNavigationComponent } from './navigation/header-navigation.component';
import { HomeComponent } from './home/home.component';
import { FooterComponent } from './footer/footer.component';
import { AboutComponent } from './about/about.component';
import { BusinessHomeComponent } from './business/business.component';
import { PricingComponent } from './pricing/pricing.component';
import { TplusAccountComponent } from './t-plus-account/t-plus-account.component';
import { CorporateAccountComponent } from './corporate/corporate.component';
import { PersonalAccountComponent } from './personal-account/personal-account.component';
import { MultiLangService } from './shared/multi.service';
import { ServiceWorkerModule } from '@angular/service-worker';
import { MobileNavComponent } from './mobile-nav/mobile-nav.component';
import { FaqAddPipe } from './shared/faqAdd.pipe';
import { PopupComponent } from './popup/popup.component';
import { PrivacyPolicyComponent } from './privacy/privacy.component';
import { AppInstallPopupComponent } from './app-install-popup/app-install-popup.component';


const appRoutes:Routes = [
  { path:'', component:HomeComponent },
  { path:'about-sombank', component:AboutComponent },
  { path:'sombank-for-businesses', component:BusinessHomeComponent },
  { path:'t-plus-account', component:TplusAccountComponent },
  { path:'sombank-pricing', component:PricingComponent },
  { path:'sombank-personal-account', component:PersonalAccountComponent},
  { path:'sombank-corporate-account', component:CorporateAccountComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderNavigationComponent,
    MobileNavComponent,
    HomeComponent,
    AboutComponent,
    BusinessHomeComponent,
    PricingComponent,
    TplusAccountComponent,
    CorporateAccountComponent,
    PersonalAccountComponent,
    FooterComponent,
    PopupComponent,
    PrivacyPolicyComponent,
    AppInstallPopupComponent,
    FaqAddPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MatDialogModule,
    BrowserAnimationsModule,
    RouterModule.forRoot( appRoutes, { scrollPositionRestoration: 'enabled' }),
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production })
  ],
  providers: [ MultiLangService ],
  bootstrap: [ AppComponent ],
  entryComponents: [ PopupComponent, PrivacyPolicyComponent, AppInstallPopupComponent ]
})
export class AppModule { }
