import { Component } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';
import { NgForm } from '@angular/forms';

@Component({
    selector:'t-plus',
    templateUrl:'./t-plus-account.component.html'
})

export class TplusAccountComponent {

    lang:string;
    content:any;

    constructor(private multi:MultiLangService){ }

    ngOnInit(){
        this.lang = this.multi.lang
        this.getContent(this.lang)
        this.multi.tlang.subscribe(
            (lang:string) => {
                this.lang = lang;
                this.getContent(lang)
            }
        )
    }

    getContent(lang:string){
        if(lang === 'eng'){
            this.content = this.multi.tplusPageContent.eng;
        }
        else if(lang === 'som'){
            this.content = this.multi.tplusPageContent.som;
        }
        else if(lang === 'arb'){
            this.content = this.multi.tplusPageContent.arb;
        }
    }

    onSubmit(f:NgForm){
        this.multi.onSubmit(f, this.lang)
    }
    
}