import { Component } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';

@Component({
    selector:'footer-content',
    templateUrl:'./footer.component.html'
})

export class FooterComponent {
    
    lang:string;
    content1:any;

    constructor(private multi:MultiLangService){}

    ngOnInit(){
        this.lang = this.multi.lang;
        this.getContent(this.lang)
        this.multi.tlang.subscribe(
            (lang:string) => {
                this.lang = lang;
                this.getContent(lang);
            }
        )
    }

    getContent(lang:string){
        if(lang === 'eng'){
            this.content1 = this.multi.footer.eng
        }
        else if(lang === 'arb'){
            this.content1 = this.multi.footer.arb
        }
        else if(lang === 'som'){
            this.content1 = this.multi.footer.som
        }
    }

    openDialog(){
        this.multi.openPrivacyPolicy(this.lang)
    }
}