import { Component } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';

@Component({
    selector:'mobile-nav',
    templateUrl:'./mobile-nav.component.html'
})

export class MobileNavComponent {
    
    mobilelang:string;
    navMenuActiveMobile:any;
  
    constructor(private multi:MultiLangService){ }
  
    ngOnInit(){
        this.mobilelang = this.multi.lang
        this.getContent(this.mobilelang)
        this.multi.tlang.subscribe(
            (lang:string) => {
                this.mobilelang = lang
                this.getContent(lang)
            }
        )
    }

    getContent(lang:string){
        if(lang === 'eng'){
            this.mobilelang = lang
            this.navMenuActiveMobile = this.multi.navMenu.eng
        }
        else if(lang === 'som'){
            this.mobilelang = lang
            this.navMenuActiveMobile = this.multi.navMenu.som
        }
        else if(lang === 'arb'){
            this.mobilelang = lang
            this.navMenuActiveMobile = this.multi.navMenu.arb
        }
    }

    openDialog(){
        this.multi.openPrivacyPolicy(this.mobilelang)
    }
  
    /* 
    onChangeMobile(){
        this.multi.tlang.subscribe(
            (lang:string) => {
                if(lang === 'eng'){
                    this.navMenuActiveMobile = this.multi.navMenu.eng
                }
                else if(lang === 'som'){
                    this.navMenuActiveMobile = this.multi.navMenu.som
                }
                else if(lang === 'arb'){
                    this.navMenuActiveMobile = this.multi.navMenu.arb
                }
            }
        )
    } 
    */
}